import React, { useRef, useEffect } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { blogPosts, BlogPost } from '../../data/blogPosts';
import { ArrowLeftIcon } from '../Icons';
import { AuthorBio } from '../AuthorBio';
import { ContactForm } from '../ContactForm';
import { ShareButtons } from '../ShareButtons';
import { generateToc } from '../utils/toc';
import { TableOfContents } from '../TableOfContents';
import Ad from '../Ad';
import AdSenseAd from '../AdSenseAd';
import { allImages } from './MyArtGallery';

interface BlogPostPageProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
    post: BlogPost;
    handleNavigate: (path: string) => void;
}

const NavCard: React.FC<{ post: BlogPost; handleNavigate: (path: string) => void; direction: 'Previous' | 'Next' }> = ({ post, handleNavigate, direction }) => {
    const href = post.type === 'recipe' ? `/recipes/${post.slug}` : `/blog/${post.slug}`;
    return (
        <a
            href={href}
            onClick={(e) => { e.preventDefault(); handleNavigate(href); }}
            className="p-4 border border-brand-border rounded-lg hover:bg-brand-pink-light/50 transition-colors group"
        >
            <p className="text-sm font-bold text-brand-pink">{direction} Article</p>
            <p className="font-semibold text-brand-text-title group-hover:underline">{post.title}</p>
        </a>
    );
};


export const BlogPostPage: React.FC<BlogPostPageProps> = ({ setActiveTool, breadcrumbs, post, handleNavigate }) => {
    const contentRef = useRef<HTMLDivElement>(null);
    const tocRef = useRef<HTMLDivElement>(null);
    const { headings, contentWithIds } = generateToc(post.content);

    useEffect(() => {
        const contentDiv = contentRef.current;
        if (!contentDiv) return;

        const handleToolClick = (e: MouseEvent) => {
            const target = e.target as HTMLElement;
            if (target.tagName === 'BUTTON' && target.dataset.toolid) {
                e.preventDefault();
                setActiveTool(target.dataset.toolid as ToolId);
            }
        };

        const handleTocClick = (e: MouseEvent) => {
            const target = e.target as HTMLAnchorElement;
            if (target.tagName === 'A' && target.hash) {
                const id = target.hash.substring(1);
                const element = document.getElementById(id);
                if (element) {
                    e.preventDefault();
                    element.scrollIntoView({ behavior: 'smooth', block: 'start' });
                }
            }
        };

        contentDiv.addEventListener('click', handleToolClick);
        const tocDiv = tocRef.current;
        tocDiv?.addEventListener('click', handleTocClick);

        return () => {
            contentDiv.removeEventListener('click', handleToolClick);
            tocDiv?.removeEventListener('click', handleTocClick);
        };
    }, [post, setActiveTool]);

    const currentIndex = blogPosts.findIndex(p => p.id === post.id);
    const prevPost = currentIndex > 0 ? blogPosts[currentIndex - 1] : null;
    const nextPost = currentIndex < blogPosts.length - 1 ? blogPosts[currentIndex + 1] : null;

    const postUrl = post.type === 'recipe' ? `https://sweetysbakery.com/recipes/${post.slug}` : `https://sweetysbakery.com/blog/${post.slug}`;

    return (
        <ToolContainer title="" description="" setActiveTool={setActiveTool} breadcrumbs={breadcrumbs}>
            <div className="max-w-3xl mx-auto">
                <button
                    onClick={() => handleNavigate(post.type === 'recipe' ? '/recipes' : '/blog')}
                    className="flex items-center gap-2 text-sm font-bold text-brand-pink hover:underline mb-4"
                >
                    <ArrowLeftIcon className="w-4 h-4" />
                    {post.type === 'recipe' ? 'Back to Recipes' : 'Back to Blog'}
                </button>

                <Ad />

                <h1 className="font-serif text-4xl font-bold text-brand-text-title">{post.title}</h1>

                <div className="mt-4 text-sm text-brand-text-body/70">
                    <span>Published on {post.date} Â· {post.readTime} read</span>
                </div>

                <Ad />

                <p className="mt-6 text-lg italic text-brand-text-body/90 border-l-4 border-brand-border pl-4">{post.excerpt}</p>

                <div className="mt-6 py-4 border-t border-b border-brand-border">
                    <ShareButtons
                        url={postUrl}
                        imageUrl={`https://sweetysbakery.com${post.imageUrl}`}
                        description={post.excerpt}
                    />
                </div>

                <div ref={tocRef}>
                    <TableOfContents headings={headings} />
                </div>

                <div ref={contentRef} className="prose prose-lg max-w-none mt-8 text-brand-text-body/90 prose-p:mb-6 prose-headings:font-serif prose-headings:font-bold prose-headings:text-brand-text-title prose-h2:text-3xl prose-h3:text-2xl prose-h4:text-xl prose-ul:pl-5 prose-li:my-2 prose-a:text-brand-pink prose-a:font-semibold prose-a:underline prose-strong:text-brand-text-title">
                    {(() => {
                        const paragraphs = post.content.split('</p>');
                        const totalParagraphs = paragraphs.length;
                        const adIndex1 = Math.floor(totalParagraphs / 3);
                        const adIndex2 = Math.floor(2 * totalParagraphs / 3);

                        return paragraphs.map((paragraph, index) => {
                            // Don't add </p> to the last empty string if split results in one
                            if (index === paragraphs.length - 1 && !paragraph.trim()) return null;

                            const contentChunk = paragraph + (index < paragraphs.length - 1 ? '</p>' : '');
                            return (
                                <React.Fragment key={index}>
                                    <div dangerouslySetInnerHTML={{ __html: contentChunk }} />
                                    {index === 0 && <AdSenseAd />}
                                    {index === adIndex1 && <Ad />}
                                    {index === adIndex2 && <Ad />}
                                </React.Fragment>
                            );
                        });
                    })()}
                </div>

                <div className="mt-12 pt-8 border-t border-brand-border space-y-12">
                    <AuthorBio handleNavigate={handleNavigate} />

                    <div>
                        <h3 className="font-serif text-2xl font-bold text-brand-text-title text-center mb-6">From My Gallery</h3>
                        <div className="grid grid-cols-2 sm:grid-cols-5 gap-2">
                            {allImages.slice(0, 10).map((img, index) => (
                                <div key={index} className="aspect-square rounded-lg overflow-hidden shadow-sm hover:opacity-90 transition-opacity">
                                    <img src={img} alt={`Gallery image ${index + 1}`} className="w-full h-full object-cover" loading="lazy" />
                                </div>
                            ))}
                        </div>
                        <div className="text-center mt-4">
                            <button onClick={() => setActiveTool('my-art')} className="text-brand-pink font-bold hover:underline">View Full Gallery â†’</button>
                        </div>
                    </div>

                    <div>
                        <h2 className="font-serif text-3xl font-bold text-brand-text-title text-center mb-6">Contact Me</h2>
                        <ContactForm />
                    </div>
                </div>

                {(prevPost || nextPost) && (
                    <div className="mt-12 pt-8 border-t border-brand-border">
                        <h2 className="font-serif text-2xl font-bold text-brand-text-title text-center mb-4">Read Next</h2>
                        <div className="grid sm:grid-cols-2 gap-4">
                            {prevPost && <NavCard post={prevPost} handleNavigate={handleNavigate} direction="Previous" />}
                            {nextPost && <NavCard post={nextPost} handleNavigate={handleNavigate} direction="Next" />}
                        </div>
                    </div>
                )}
            </div>
        </ToolContainer>
    );
};
