import React from 'react';
import { Footer } from '../Footer';
import { ArrowLeftIcon } from '../Icons';
import { ToolId, toolIdToSlug } from '../utils/slugs';
import AdSenseAd from '../AdSenseAd';
import Ad from '../Ad';

interface ToolContainerProps {
    title: string;
    description: string;
    children: React.ReactNode;
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const ToolContainer: React.FC<ToolContainerProps> = ({ title, description, children, setActiveTool, breadcrumbs }) => {
    return (
        <div className="p-4 sm:p-6 md:p-10 fade-in">
            <div className="flex justify-between items-start">
                {breadcrumbs}
            </div>
            <header className="mb-8 max-w-4xl">
                <h1 className="font-serif text-4xl md:text-5xl font-bold text-brand-text-title relative pb-2">
                    {title}
                </h1>
                <p className="mt-4 text-lg text-brand-text-body/80 max-w-3xl">{description}</p>
            </header>

            <AdSenseAd />

            <div className="bg-white p-6 sm:p-8 rounded-2xl border border-brand-border shadow-sm">
                {children}
            </div>

            <Ad />

            <Footer setActiveTool={setActiveTool} />
        </div>
    );
};
