import React, { useState, useEffect } from 'react';
import { ToolContainer } from './ToolContainer';
import type { ToolId } from '../utils/slugs';
import { XIcon, ChevronRightIcon, ArrowLeftIcon } from '../Icons';
import { ImageModal } from '../ImageModal';

export const allImages = Array.from({ length: 1151 }, (_, i) =>
    `https://sweetysbakery.com/images/Sweeetys%20Bakery%20IMG%20(${i + 5}).JPG`
);

const IMAGES_PER_PAGE = 20;

interface MyArtGalleryProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
}

export const MyArtGallery: React.FC<MyArtGalleryProps> = ({ setActiveTool, breadcrumbs }) => {
    const images = allImages;
    const [visibleCount, setVisibleCount] = useState(IMAGES_PER_PAGE);
    const [selectedImageIndex, setSelectedImageIndex] = useState<number | null>(null);

    const openModal = (index: number) => setSelectedImageIndex(index);
    const closeModal = () => setSelectedImageIndex(null);

    return (
        <>
            <ToolContainer
                title="My Art Gallery"
                description="A gallery of beautiful, custom-made cakes, cookies, and pastries. Upload your own creations to build your personal portfolio."
                setActiveTool={setActiveTool}
                breadcrumbs={breadcrumbs}
            >
                <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
                    {images.slice(0, visibleCount).map((img, index) => (
                        <div key={`${img}-${index}`} className="aspect-square bg-brand-pink-light rounded-lg overflow-hidden shadow-sm border border-brand-border group relative">
                            <img
                                src={img}
                                alt={`Bakery creation ${index + 1}`}
                                loading="lazy"
                                decoding="async"
                                className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-110 cursor-pointer"
                                onClick={() => openModal(images.indexOf(img))}
                                onError={(e) => { (e.currentTarget.parentElement as HTMLElement).style.display = 'none'; }}
                            />
                        </div>
                    ))}
                    {images.length === 0 && (
                        <div className="col-span-full text-center py-16 bg-brand-pink-light/60 rounded-lg border border-brand-border">
                            <p className="font-bold text-brand-text-title">Your gallery is empty.</p>
                            <p className="text-sm text-brand-text-body/80">Upload an image to get started!</p>
                        </div>
                    )}
                </div>
                {visibleCount < images.length && (
                    <div className="text-center mt-8">
                        <button onClick={() => setVisibleCount(prev => Math.min(prev + IMAGES_PER_PAGE, images.length))} className="bg-white text-brand-pink font-bold py-2 px-6 rounded-full shadow-md hover:bg-brand-pink-light transition-colors border border-brand-border">Load More Images</button>
                    </div>
                )}
            </ToolContainer>
            {selectedImageIndex !== null && (
                <ImageModal
                    imageUrl={images[selectedImageIndex]}
                    onClose={closeModal}
                    onPrev={() => setSelectedImageIndex((selectedImageIndex - 1 + images.length) % images.length)}
                    onNext={() => setSelectedImageIndex((selectedImageIndex + 1) % images.length)}
                    currentIndex={selectedImageIndex}
                    totalImages={images.length}
                />
            )}
        </>
    );
};
