import React from 'react';
import { ToolContainer } from './ToolContainer';
import { navConfig } from '../Sidebar';
import { toolIdToSlug, ToolId } from '../utils/slugs';

interface AllToolsProps {
    setActiveTool: (toolId: ToolId) => void;
    breadcrumbs: React.ReactNode;
    categoryId?: string;
}

export const AllTools: React.FC<AllToolsProps> = ({ setActiveTool, breadcrumbs, categoryId }) => {
    const filteredConfig = categoryId
        ? navConfig.filter(c => c.id === categoryId)
        : navConfig;

    const pageTitle = categoryId && filteredConfig.length > 0
        ? filteredConfig[0].title
        : "All Tools & Calculators";

    const pageDescription = categoryId && filteredConfig.length > 0
        ? `Explore our collection of ${filteredConfig[0].title.toLowerCase()}. Free, accurate, and easy to use.`
        : "A complete directory of all our baking calculators, converters, and resources. Find exactly what you need for your next baking project.";

    return (
        <ToolContainer
            title={pageTitle}
            description={pageDescription}
            setActiveTool={setActiveTool}
            breadcrumbs={breadcrumbs}
        >
            <div className="space-y-12">
                {filteredConfig.map((category) => (
                    <div key={category.id} className="space-y-6">
                        <div className="flex items-center gap-3 border-b border-brand-border pb-2">
                            <span className="text-brand-pink">{category.icon}</span>
                            <h2 className="text-2xl font-serif font-bold text-brand-text-title">
                                {category.title}
                            </h2>
                        </div>

                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                            {category.tools.map((tool) => (
                                <a
                                    key={tool.id}
                                    href={toolIdToSlug(tool.id as ToolId)}
                                    onClick={(e) => {
                                        e.preventDefault();
                                        setActiveTool(tool.id as ToolId);
                                    }}
                                    className="block p-6 bg-white rounded-xl border border-brand-border shadow-sm hover:shadow-md hover:border-brand-pink/50 transition-all duration-300 group"
                                >
                                    <div className="flex items-start gap-4">
                                        <div className="bg-brand-pink-light text-brand-pink p-3 rounded-full flex-shrink-0 group-hover:scale-110 transition-transform">
                                            {tool.icon}
                                        </div>
                                        <div>
                                            <h3 className="font-serif font-bold text-brand-text-title group-hover:text-brand-pink transition-colors">
                                                {tool.title}
                                            </h3>
                                            <p className="text-xs text-brand-text-body/60 mt-1">
                                                Click to open tool
                                            </p>
                                        </div>
                                    </div>
                                </a>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </ToolContainer>
    );
};
